import Vue from 'vue'

function cap(str){
	return str.charAt(0).toUpperCase()+str.slice(1)
}
const req = require.context(
	'.',true,/\.vue$/
)
req.keys().forEach(fileName => {
	const com = req(fileName)
//	const conN = cap(
//		fileName.replace(/^\.\//,'').replace(/\.\w+$/,'')
//	)
	const conN = com.default.name
	Vue.component(conN,com.default || com)
})




