import vueToast from './toast.vue'

const toast = {
	install(Vue,Options){
		var index = 1;
		var team = [];
		var offsettop = 126;
		Vue.prototype.$toast = function(msg){
			
			let tmsg = Vue.extend(vueToast);
			let mmm = new tmsg().$mount()
			mmm.msg = msg
			team.push(mmm)
			
			let dom = mmm.$el
			let body = document.body
			
			body.appendChild(dom);
			mmm.top = (team.length-1) * 40 + 126
			mmm.show = false
			setTimeout(function(){
				team.shift()
				body.removeChild(dom)
				team.forEach((v,i) => {
					v.top -= 40
				})
			},2000)
		}
	}
};

export default toast


