import Vue from 'vue'
import Router from 'vue-router'
import apple from 'components/apple/apple'
import pear from 'components/pear/pear'
import peach from 'components/peach/peach'
import orange from 'components/orange/orange'
import home from 'components/home/home'
import notDefind from 'components/404/404'

Vue.use(Router)
const router = new Router({
  routes: [
  	{ path: '/', redirect: '/home' },
  	{ 
  		name:'home',
  		path:'/home',
  		component:home
  	}
  ]
})
setTimeout(function(){
	router.addRoutes([{
   		name:'apple',
   		path:'/apple',
   		component:apple
   	},
   	{
   		name:'pear',
   		path:'/pear',
   		component:pear,
   		
   	},
   	{
   		name:'peach',
   		path:'/peach',
   		component:peach
   	},
   	{
   		name:'orange',
   		path:'/orange',
   		component:orange
   	},
   	{
   		name:'404',
   		path:'*',
   		component:notDefind
   	}])
},500)

//router.beforeEach((to, from, next) => {
//	if(to.name =='pear' || to.name == 'apple'){
//			next()
//	}
//	
//})

export default router


