<template>
	<div class="switch-box">
		<div class="switch" :class="{'change-move':value}" @click="changeM">
			<span class="pre"></span>
		</div>
	</div>
	
</template>

<script>
	export default {
		name:'Mswitch',
		props:{
			value:Boolean,
			size:String
		},
		data(){
			return {
				
			}
		},
		methods:{
			changeM(){
				var newVlue = !this.value
				this.$emit('input',newVlue)
			}
		},
		created(){
		}
	}
</script>

<style lang="scss">
	.switch-box{
		display: inline-block;
		vertical-align: middle;
	}
	.switch{
		width: 40px;
		height: 20px;
		border-radius: 10px;
		display: inline-block;
		vertical-align: middle;
		background: #dcdfe6;
		position: relative;
		transition: all .3s;
		& input{
			position: absolute;
			width: 0;
			height: 0;
			opacity: 0;
		}
		& span{
			cursor: pointer;
			position: absolute;
			left: 2px;
			top: 2px;
			width: 16px;
			height: 16px;
			border-radius: 50%;
			background: #fff;
			display: inline-block;
			transition: all .3s;
		}
	}
	.change-move{
		& span{
			transform: translate3d(20px,0,0);
		}
		background: #409eff;
		
	}
</style>


