<template>
	<div id="mheader">
		<ul>
			<router-link tag="li" :to="{ name: item.mid, params: { pa: item }}" v-for="(item,index) in list">
				{{item.name}}
			</router-link>
			<!--<li v-for="(item,index) in list">
				{{item.name}}
			</li>-->
		</ul>
	</div>
</template>

<script>
	export default{
		name:'mheader',
		data(){
			return {
				list: []
			}
		},
		methods:{
			query(){
				var _this = this;
				setTimeout(function(){
					_this.list = [
						{
							mid:'apple',
							name:'苹果'	
						},
						{
							mid:'pear',
							name:'梨子'	
						},
						{
							mid:'peach',
							name:'桃子'	
						},
						{
							mid:'orange',
							name:'橙子'	
						},
					]
				},1000)
			}
		},
		created(){
			this.$store.commit('SET_TAB',[1])
			console.log(this.$store)
			
			this.query()
		},
		myOption:'zk',
		mykk:'ddddddd'
	}
</script>

<style lang="scss">
	#mheader{
		ul{
			width: 240px;
			li{
				padding: 10px 5px;
				&:hover{
					background: cornflowerblue;
					color: #fff;
				}
			}
		}
	}
</style>


