<template>
	<div class="input">
		<input type="text" @input='out($event)' :value='value'/>
	</div>
</template>

<script>
	export default {
		name:'Minput',
		props:{
			value:{
				type:String,
				default(){
					return ''
				}
			}
			
		},
		data(){
			return {
				
			}
		},
		methods:{
			out(e){
//				console.log(this.message)
				this.$emit('input',e.target.value)
			}
		},
		created(){
		}
	}
</script>
	
<style lang="scss">
	.input{
		display: inline-block;
		vertical-align: middle;

		width: 200px;
		input{
			color: #606266;
			padding: 0 15px;
			border-radius: 4px;
			border: 1px solid #ddd;
			width: 100%;
			height: 38px;
			transition: all .2s;
			&:focus{
				outline: none;
				border: 1px solid #409eff;
			}
		}
	}
	
</style>


