<template>
	<div class="m-checkbox" 
		:class="ischeckAll == 'ui_config'?'ui':(ischeckAll?'ui-check':'ui-none')">
		<label>
			<input type="checkbox" 
			v-model="newvalue" 
			:value="cvalue" 
			@change="change" 
			/>
			<span class="input-radio">
				<slot></slot>
			</span>
		</label>
	</div>
</template>

<script>
	export default {
		name:'Mcheckbox',
		props:{
			value:{
				type:[Array,Boolean,String],
				default(){
					return []
				}
			},
			cvalue:{
				type:String,
				default(){
					return ''
				}
			}
		},
		data(){
			return {
				newvalue:this.value
			}
		},
		computed:{
			ischeckAll(){
				var isArray = typeof(this.value) == 'object';
				var status = false;
				if(isArray){
					status = this.value.indexOf(this.cvalue) != -1
				}else{
					status = this.value
				}
				return status
			}
		},
		watch:{
			value(v){
				this.newvalue = v
			}
			
		},
		methods:{
			change(){
				var value = this.newvalue
				this.$emit('input',value)
			}
		}
	}
</script>

<style lang="scss">
	.ui{
		/*border: 1px solid red;*/
		background: blue;
	}
	.ui-check{
		/*border: 1px solid red;*/
		background: green;
	}
	.ui-none{
		/*border: 1px solid red;*/
		background: red;
	}
	.m-checkbox{
		position: relative;
		input{
			opacity: 0;
			visibility: hidden;
			vertical-align: middle;
		}
		display: inline-block;
		vertical-align: middle;
		position: relative;
		cursor: pointer;
		
		.input-radio{
			display: inline-block;
			vertical-align: middle;
		}
		.input-radio{
			padding-left: 10px;
		}
	}



</style>


