import Vue from 'vue'
import Toast from './toast'
Vue.use(Toast)
var callback = function(msg){
	
}
Vue.mixin({
  created() {
    var myOption = this.$options.myOption
    if (myOption) {
      console.log(myOption)
    }
    this.logger('from methods')
  },
  methods:{
  	logger:callback
  }
})

Vue.mixin({
  created: function () {
    var mykk = this.$options.mykk
    if (mykk) {
      console.log(mykk)
    }
  }
})



