import * as types from './mutation-types'
export const randomPlay = function ({commit}) {
  
  return new Promise((resolve, reject) => {
      setTimeout(() => {
      	var list = [
						{
							mid:'apple',
							name:'苹果'	
						},
						{
							mid:'pear',
							name:'梨子'	
						},
						{
							mid:'peach',
							name:'桃子'	
						},
						{
							mid:'orange',
							name:'橙子'	
						},
					]
        commit(types.SET_TAB, list)
        resolve()
      }, 1000)
    })
  
}


