<template>
	<div class="m-radio" 
		@click="change" 
		:class="{'input-radio-check':isCheck,'input-radio-disabled-check':isCheck&&(!!disabled),'input-radio-disabled':!isCheck&&(!!disabled)}" 
		>
		<span class="input-radio-ui">
			<span class="radio-inner"></span>
		</span>
		<span class="input-radio">
			<slot></slot>
		</span>
	</div>
</template>

<script>
	export default {
		name:'Mradio',
		props:{
			rvalue:[String, Number,Boolean],
			value:[String, Number,Boolean],
			disabled:{
				type:[Boolean,String],
				default(){
					return false;
				}
			}
		},
		data(){
			return {
				newValue:null
			}
		},
		computed:{
			isCheck(){
				return this.rvalue === this.value
			}
		},
		methods:{
			change(){
				if(this.disabled) return
				this.newValue = this.rvalue
				this.$emit('input',this.newValue)
			}
		},
		created(){
			console.log(this.disabled)
		}
	}
</script>

<style lang="scss">
	.m-radio{
		display: inline-block;
		vertical-align: middle;
		position: relative;
		cursor: pointer;
		.input-radio,.input-radio-ui{
			display: inline-block;
			vertical-align: middle;
		}
		.input-radio{
			padding-left: 10px;
		}
		.input-radio-ui{
			white-space: nowrap;
		    line-height: 1;
		    position: relative;
		    width: 14px;
		    height: 12px;
		    
		    .radio-inner{
		    	display: inline-block;
		    	width: 14px;
		    	height: 14px;
		    	border: 1px solid #dcdfe6;
		    	border-radius: 50%;
		    	position: relative;
		    	border: 1px solid #dcdfe6;
		    	transition: all .3s;
		    	&:after{
		    		content: " ";
		    		display: inline-block;
		    		position: absolute;
		    		top: 50%;
		    		left: 50%;
		    		transform: translate(-50%,-50%) scale(0);
		    		background: #fff;
		    		width: 4px;
		    		height: 4px;
		    		border-radius: 50%;
		    		transition: all .3s;
		    	}
		    }
		    
		}
		
	}
	.input-radio-check{
		.radio-inner{
			background: #409eff;
			border: none;
			&:after{
				transform: translate(-50%,-50%) scale(1)!important;
			}
		}
		.input-radio{
			color: #409eff;
		}
		
	}
	.input-radio-disabled-check{
		.radio-inner{
			background: #e4e7ed;
			border: none;
			&:after{
				background: #c0c4cc;
				transform: translate(-50%,-50%) scale(1)!important;
			}
		}
		.input-radio{
			color: #e4e7ed;
		}
		
	}
	.input-radio-disabled{
		.radio-inner{
			background: #e4e7ed;
			border: none;
		}
		.input-radio{
			color: #e4e7ed;
		}
		
	}
</style>


