<template>
	<div class="toast" :style="{top:top+'px'}" v-show="top>0">
		<span>{{msg}}</span>
	</div>
</template>

<script>
	export default {
		name:'toast',
		data(){
			return {
				msg:'moren',
				top:0,
				show:true
			}
		}
	}
</script>

<style lang="scss">
	.toast{
		position: fixed;
		left: 50%;
		transform: translateX(-50%);
		top: 50%;
		background: rgba(000,000,000,.7);
		color: #fff;
		text-align: center;
		padding: 5px;
		transition: all .3s;
	}
	.hideStart{
		transform: translateX(100%);
	}
</style>


