<template>
	<div id="apple">
		apple
		<a @click="show">显示Toast</a>
		<div class="input-content">
			<Minput v-model="d"></Minput>
			<span>{{d}}</span>
		</div>
		<div class="input-content">
			<Mswitch v-model="six" size="small"></Mswitch>
			<span>{{six}}</span>
			<a @click="check">checkSweitch</a>
		</div>
		<div class="input-content">
			<Mradio v-model="classes" rvalue="数学" disabled>
				这是数学
			</Mradio>
			<Mradio v-model="classes"  rvalue="体育" title="asdasd">
				这是体育
			</Mradio>
			<Mradio v-model="classes"  rvalue="英语">
				这是英语
			</Mradio>
			<div>{{classes}}</div>
		</div>
		<div class="input-content">
			<ul>
				<li>
					<Mcheckbox v-model="isCheckAll">
						全选
					</Mcheckbox>
					
				</li>
				<li v-for="(item,index) in list">
					<Mcheckbox v-model="selectCheck" :cvalue='item.id'></Mcheckbox>
					{{item.name}}
				</li>
			</ul>
			{{selectCheck}}
		</div>
	</div>
</template>

<script>
	export default {
		name:'apple',
		data(){
			return {
				d:'sss',
				six:true,
				classes:'数学',
				selectCheck:['123'],
				list:[
					{
						id:'123',
						name:'123'
					},
					{
						id:'345',
						name:'345'
					},
					{
						id:'789',
						name:'789'
					},
				]
			}
		},
		computed:{
			isCheckAll:{
				set(v) {
					var allLength = this.list.length;
			        var newLength = this.selectCheck.length;
					var id = getid(this.list)
			        if(v){
			        	this.selectCheck = id
			        }else{
			        	this.selectCheck = []
			        }
			    },
				get() {
			        var allLength = this.list.length;
			        var newLength = this.selectCheck.length;
					var def = newLength == 0 ? false : (newLength == allLength? true : 'ui_config')
			        return def
			   }
			}
		},
		created(){

		},
		methods:{
			show(){
				this.$toast('wowowowow')
			},
			check(){
				this.six = !this.six	
			}
		}	
	}
	function getid(arr){
		var newArr = [];
		arr.forEach((v,i) => {
			newArr.push(v.id)
		})
		return newArr
	}
</script>

<style lang="scss">
	.input-content{
		margin: 10px;
	}
</style>


